import axios from 'axios'
import React from 'react'
import { toast } from 'react-toastify'

const QuoteCard = ({ _id, author, text, openForm, setFormType, setUpdateID, setQuoteHandler }) => {

    const handleDelete = async (_id) => {
        try {
            await axios.delete(`/api/quotes/${_id}`)
            toast.success('Quote Deleted Successfully.')
        } catch (error) {
            console.log("Not Deleted, Some Error Occured.")
            toast.error('Quote Deleted Successfully.')
        }
        
    }

    const handleEdit = async (_id) => {
        const quoteToUpdate = await (await axios.get(`/api/quotes/${_id}`)).data
        setQuoteHandler({ quote: quoteToUpdate.quote, author: quoteToUpdate.author })
        setUpdateID(_id)
        openForm()
        setFormType('update')
    }


    return (
        <div className="prompt_card">
            <span className='text-inter text-5xl text-gray-400'>
                <i className="fa-solid fa-quote-left"></i>
            </span>
            <p className="my-4 font-satoshi text-[18px] text-gray-600">
                {text}
            </p>
            <p
                className="text-end font-inter text-sm text-slate-400"
            >
                ~ <span className='italic text-sm'>
                    {author}
                </span>
            </p>

            <div className="mt-5 flex justify-end items-center gap-4 border-t border-gray-100 pt-3">
                <p
                    className="font-inter text-sm text-green-400 cursor-pointer"
                    onClick={() => handleEdit(_id)}
                >
                    <i className="fa-regular fa-pen-to-square"></i>
                </p>
                <p
                    className="font-inter text-sm text-red-500 cursor-pointer"
                    onClick={() => handleDelete(_id)}
                >
                    <i className="fa-solid fa-trash"></i>
                </p>
            </div>
        </div>
    )
}

export default QuoteCard