import React from "react";
import {
    Button,
    Dialog,
    DialogHeader,
    DialogBody,
    DialogFooter,
    Input,
    Textarea,
    Typography,
} from "@material-tailwind/react";
import axios from "axios";
import { toast } from "react-toastify";

let updateQuote;

export default function MessageDialog({ type, handleOpen, open, quoteHandler, handleUpdate, setQuoteHandler }) {
    const initialQuote = { quote: '', author: '' }


    const handleChange = (e) => {
        setQuoteHandler({ ...quoteHandler, [e.target.name]: e.target.value })
    }

    const handleSubmit = async () => {
        if (!quoteHandler.author || !quoteHandler.quote || quoteHandler.quote.length < 10) {
            console.error('|Quote (Form)| \n --> Not Submitted, Due to Invalid Data.')
            return;
        }

        try {
            await axios.post('/api/quotes', quoteHandler)
            setQuoteHandler(initialQuote)
            handleOpen()
            toast.success('Quote Submitted Successfully.')
        } catch (error) {
            console.log(error)
            toast.error('Sorry, Query is Not Added. Maybe some Error.')
        }
    }
    

    return (
        <>

            <Dialog open={open} size="xs" handler={handleOpen}>
                <div className="flex items-center justify-between">
                    <DialogHeader className="flex flex-col items-start">
                        <Typography className="mb-1 capitalize" variant="h4">
                            {type} Quote
                        </Typography>
                    </DialogHeader>
                    <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 24 24"
                        fill="currentColor"
                        className="mr-3 h-5 w-5 cursor-pointer"
                        onClick={handleOpen}
                    >
                        <path
                            fillRule="evenodd"
                            d="M5.47 5.47a.75.75 0 011.06 0L12 10.94l5.47-5.47a.75.75 0 111.06 1.06L13.06 12l5.47 5.47a.75.75 0 11-1.06 1.06L12 13.06l-5.47 5.47a.75.75 0 01-1.06-1.06L10.94 12 5.47 6.53a.75.75 0 010-1.06z"
                            clipRule="evenodd"
                        />
                    </svg>
                </div>
                <DialogBody>
                    <Typography className="mb-10 -mt-7 " color="gray">
                        Fill form below, and remember all fields are required.
                    </Typography>
                    <div className="grid gap-6">
                        <Textarea
                            name="quote"
                            value={quoteHandler.quote}
                            onChange={handleChange}
                            label="Write Quote"
                        />
                        <Input
                            name="author"
                            value={quoteHandler.author}
                            onChange={handleChange}
                            label="Author (Orignal)"
                        />
                    </div>
                </DialogBody>
                <DialogFooter className="space-x-2">
                    <Button variant="text" color="gray" onClick={handleOpen}>
                        cancel
                    </Button>
                    <Button variant="gradient" className="uppercase" color="gray" onClick={type === 'add' ? handleSubmit : () => handleUpdate()}>
                        {type} Quote
                    </Button>
                </DialogFooter>
            </Dialog>
        </>
    );
}

export { updateQuote };