import React, { useEffect, useState } from 'react'


const SearchBar = ({ quoteData, setSearchedData }) => {
    const [toBeSearch, setToBeSearch] = useState('')

    // Search states
    const [searchTimeout, setSearchTimeout] = useState(null);
    const [searchedResults, setSearchedResults] = useState([]);

    useEffect(() => setSearchedData(searchedResults), [searchedResults])
    const filterPrompts = (searchtext) => {
        const regex = new RegExp(searchtext, "i");

        return quoteData.filter(
            (item) =>
                regex.test(item.author) || regex.test(item.quote)
        );
    };

    const handleSearchInput = (e) => {
        setToBeSearch(e.target.value) 
        !e.target.value && setSearchedData([])
    }
    
    const handleSearch = () => {
        clearTimeout(searchTimeout);

        // debounce method
        setSearchTimeout(
            setTimeout(() => {
                const searchResult = filterPrompts(toBeSearch);
                setSearchedResults(searchResult);
            }, 500)
        );
    };

    return (
        <>
            <section className='flex min-w-md w-full sm:w-3/4 lg:w-2/3 mx-auto px-4'>
                <input type="text" onChange={(e) => handleSearchInput(e)} placeholder='Search here' className='search_input' />
                <button
                    onClick={handleSearch}
                    className='bg-black py-2 px-3 text-white rounded-r-sm shadow-lg text-sm'
                    style={{
                        boxShadow: [
                            '0 8px 10px 0 #ddd',
                            'inset -1px 2.5px 2px 0 #ccc'
                        ],
                    }}
                >
                    Search
                </button>
            </section>
        </>
    )
}

export default SearchBar