import React, { Suspense, useEffect, useState } from 'react'
import QuoteCard from '../components/QuoteCard'
import Form from '../components/Form';
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css';
import SearchBar from '../components/SearchBar';


const Index = () => {
  const [quoteHandler, setQuoteHandler] = useState({})
  const [updateID, setUpdateID] = useState('')
  const [open, setOpen] = useState(false);
  const [formType, setFormType] = useState('add');
  const [quoteData, setQuoteData] = useState([]);
  const [searchedData, setSearchedData] = useState([]);

  const getData = async () => {
    const responseData = await axios.get('/api/quotes')
    setQuoteData(responseData.data)
  }

  useEffect(() => { getData() }, [])

  const handleUpdate = async () => {

    if (!quoteHandler.author || !quoteHandler.quote || quoteHandler.quote.length < 10) {
      console.error('|Quote (Form)| \n --> Not Submitted, Due to Invalid Data.')
      return;
    }
    console.log(updateID)
    try {
      await axios.put(`/api/quotes/${updateID}`, quoteHandler)
      setQuoteHandler({ quote: '', author: '' })
      toast.success('Quote Updated Successfully.')
      setOpen(false)
    } catch (error) {
      console.log(error)
      toast.error('Sorry, Query is Not Updated. Maybe some Error.')
    }
  }

  return (
    <div>
      <ToastContainer />
      <SearchBar quoteData={quoteData} setSearchedData={setSearchedData} />
      <hr className='max-w-lg mx-auto bg-slate-400 my-6' />
      <div className='flex justify-end items-center'>
        <span className='text-xl mr-12 text-gray-100 bg-black rounded-sm px-1 py-0'
          style={{
            boxShadow: [
              '0 0px 10px 0 #ddd',
              'inset -1px 2.5px 2px 0 #ccc'
            ],
          }}
          onClick={(e) => {
            e.target.classList.add('fa-spin')
            getData()
            setTimeout(() => e.target.classList.remove('fa-spin'), 2023)

          }} >
          <i className="fa-solid fa-arrow-rotate-right"></i>
        </span>
        <button
          className="outline_btn"
          onClick={() => {
            setOpen(true);
            setFormType('add')
          }}
        >
          Add Quote
        </button>
        <Form
          type={formType}
          quoteHandler={quoteHandler}
          handleUpdate={handleUpdate}
          setQuoteHandler={setQuoteHandler}
          open={open}
          handleOpen={() => setOpen(prev => !prev)}
        />
      </div>
      <Suspense fallback={<center>Loading Data...</center>} className='prompt_layout'>
        {
          searchedData.length > 0
            ?
            searchedData?.map((ele) => (
              <QuoteCard
                key={ele._id}
                _id={ele._id}
                author={ele.author}
                text={ele.quote}
                setUpdateID={setUpdateID}
                openForm={() => setOpen(true)}
                setFormType={setFormType}
                setQuoteHandler={setQuoteHandler}
              />)
            )
            :
            quoteData.length > 0
              ?
              quoteData.map((ele) => (
                <QuoteCard
                  key={ele._id}
                  _id={ele._id}
                  author={ele.author}
                  text={ele.quote}
                  setUpdateID={setUpdateID}
                  openForm={() => setOpen(true)}
                  setFormType={setFormType}
                  setQuoteHandler={setQuoteHandler}
                />)
              )
              :
              <>
                <div className='mt-8 pl-2 md:text-2xl text-lg text-gray-600/80'>
                  Sorry, No Data to show
                </div>
              </>
        }
      </Suspense>
    </div>
  )
}

export default Index